"use client"

import { useEffect, useRef, useState } from "react"

export function CustomCursor() {
  const cursorRef = useRef<HTMLDivElement>(null)
  const [isVisible, setIsVisible] = useState(false)

  useEffect(() => {
    // Funci贸n que mueve el cursor
    const moveCursor = (e: MouseEvent) => {
      if (cursorRef.current) {
        // Centramos el cursor (restamos la mitad del tama帽o)
        // Si tu cursor mide 80px, restamos 40px
        const x = e.clientX - 40 
        const y = e.clientY - 40
        
        cursorRef.current.style.transform = `translate3d(${x}px, ${y}px, 0)`
      }
      
      // Mostrar cursor solo cuando se mueva el mouse por primera vez
      if (!isVisible) setIsVisible(true)
    }

    const handleMouseDown = () => {
        if (cursorRef.current) cursorRef.current.style.transform += ` scale(0.9)`
    }
    
    const handleMouseUp = (e: MouseEvent) => {
        if (cursorRef.current) {
            const x = e.clientX - 40 
            const y = e.clientY - 40
            cursorRef.current.style.transform = `translate3d(${x}px, ${y}px, 0) scale(1)`
        }
    }

    window.addEventListener("mousemove", moveCursor)
    window.addEventListener("mousedown", handleMouseDown)
    window.addEventListener("mouseup", handleMouseUp)

    return () => {
      window.removeEventListener("mousemove", moveCursor)
      window.removeEventListener("mousedown", handleMouseDown)
      window.removeEventListener("mouseup", handleMouseUp)
    }
  }, [isVisible])

  // Si es m贸vil/tablet no mostramos cursor personalizado (es molesto en touch)
  if (typeof window !== "undefined" && window.matchMedia("(hover: none)").matches) {
    return null
  }

  return (
    <div
      ref={cursorRef}
      className="fixed top-0 left-0 pointer-events-none z-[9999] opacity-0 transition-opacity duration-300"
      style={{ 
        opacity: isVisible ? 1 : 0,
        width: '80px',   //  AQU CAMBIAS EL TAMAO
        height: '80px'   //  AQU CAMBIAS EL TAMAO
      }}
    >
      {/* Video en bucle y sin fondo (si es posible) */}
      <video
        src="/cursor-video.mp4" // Aseg煤rate de que este nombre coincida con tu archivo en public
        autoPlay
        loop
        muted
        playsInline
        className="w-full h-full object-cover rounded-full shadow-lg shadow-purple-500/30" // Estilos extra: Sombra y redondeado
      />
    </div>
  )
}